<template>
	<div>
		<!--  -->
		<div :style='{"padding":"12px 20px","margin":"0 auto","borderRadius":"4px","background":"#f5f7fa","borderWidth":"0px","width":"86%","borderStyle":"solid"}' class="breadcrumb-preview">
			<el-breadcrumb :separator="'>'" :style='{"fontSize":"14px","lineHeight":"1","justifyContent":"flex-start","display":"flex"}'>
