    <div class="big" :style='{"border":"1px solid #eee","margin":"0","background":"#fff","width":"100%","position":"relative","height":"500px","order":"1","boxShadow":"0 2px 12px 0 rgba(0,0,0,0.1)","overflow":"hidden"}'>
        <img id="big" 
            :style='{"objectFit":"contain","top":"0px","left":"0px","display":"block","width":"100%","position":"absolute","height":"100%","zIndex":"1"}' 
