</div>

<!--  -->
<div class="thumbnail-container" v-if="detailBanner.length > 1" :style='{"width":"58%","margin":"10px 0 0","padding":"0","display":"flex","flexWrap":"wrap","justifyContent":"flex-start"}'>
    <div :style='{"border":"1px solid #eee","width":"120px","margin":"0 10px 10px 0","position":"relative","background":"#fff","height":"90px","cursor":"pointer","transition":"all 0.3s","overflow":"hidden"}' 
        v-for="(item, index) in detailBanner" 
        :key="index" 
        @click="swiperClick3(item.substr(0,4)=='http'?item:baseUrl + item)">
        <img :style='{"objectFit":"contain","top":"0px","left":"0px","display":"block","width":"100%","position":"absolute","height":"100%","zIndex":"1"}' 
            v-if="item.substr(0,4)=='http'" 
            :src="item" 
            class="image">
        <img :style='{"objectFit":"contain","top":"0px","left":"0px","display":"block","width":"100%","position":"absolute","height":"100%","zIndex":"1"}' 
            v-else 
            :src="baseUrl + item" 
            class="image">
    </div>
</div>

	
			<!--  -->
			<div class="attr" :style='{"minHeight":"500px","padding":"20px","margin":"0","flexWrap":"wrap","background":"#fff","display":"flex","width":"40%","position":"relative","justifyContent":"space-between","order":"2","boxShadow":"0 2px 12px 0 rgba(0,0,0,0.1)","borderRadius":"4px"}'>
				<div class="info" :style='{"width":"100%","padding":"0px","margin":"0","background":"#fff","flex":"1","order":"2"}'>
					<div class="item" :style='{"padding":"0","margin":"0 0 20px 0","overflow":"hidden","alignItems":"center","borderRadius":"0px","background":"none","display":"flex","lineHeight":"auto","justifyContent":"space-between","height":"auto"}'>
						<div :style='{"padding":"0px","overflow":"hidden","color":"#333","display":"block","width":"100%","fontSize":"24px","fontWeight":"600","lineHeight":"1.4"}'>
							:{{detail.chongwumingcheng}}
						</div>
						
					</div>
					
					<!--  -->
					<div class="info-section" :style='{"marginBottom":"20px"}'>
						<div class="item" :style='{"padding":"10px","margin":"0 0 10px 0","borderBottom":"1px solid #eee","background":"none","borderWidth":"0px","display":"flex","borderStyle":"solid","justifyContent":"spaceBetween"}'>
							<div class="lable" :style='{"padding":"0 10px","color":"#666","textAlign":"left","width":"100px","fontSize":"14px","lineHeight":"24px","height":"auto"}'>:</div>
							<div :style='{"padding":"0","fontSize":"14px","lineHeight":"24px","color":"#333","flex":"1","height":"auto"}'>{{detail.chongwuleixing}}</div>
						</div>
						<div class="item" :style='{"padding":"10px","margin":"0 0 10px 0","borderBottom":"1px solid #eee","background":"none","borderWidth":"0px","display":"flex","borderStyle":"solid","justifyContent":"spaceBetween"}'>
							<div class="lable" :style='{"padding":"0 10px","color":"#666","textAlign":"left","width":"100px","fontSize":"14px","lineHeight":"24px","height":"auto"}'>:</div>
							<div :style='{"padding":"0","fontSize":"14px","lineHeight":"24px","color":"#333","flex":"1","height":"auto"}'>{{detail.pinzhong}}</div>
						</div>
						<div class="item" :style='{"padding":"10px","margin":"0 0 10px 0","borderBottom":"1px solid #eee","background":"none","borderWidth":"0px","display":"flex","borderStyle":"solid","justifyContent":"spaceBetween"}'>
							<div class="lable" :style='{"padding":"0 10px","color":"#666","textAlign":"left","width":"100px","fontSize":"14px","lineHeight":"24px","height":"auto"}'>:</div>
							<div :style='{"padding":"0","fontSize":"14px","lineHeight":"24px","color":"#333","flex":"1","height":"auto"}'>{{detail.xingbie}}</div>
						</div>
						<div class="item" :style='{"padding":"10px","margin":"0 0 10px 0","borderBottom":"1px solid #eee","background":"none","borderWidth":"0px","display":"flex","borderStyle":"solid","justifyContent":"spaceBetween"}'>
							<div class="lable" :style='{"padding":"0 10px","color":"#666","textAlign":"left","width":"100px","fontSize":"14px","lineHeight":"24px","height":"auto"}'>:</div>
							<div :style='{"padding":"0","fontSize":"14px","lineHeight":"24px","color":"#333","flex":"1","height":"auto"}'>{{detail.nianling}}</div>
						</div>


						<div class="item" :style='{"padding":"10px","margin":"0 0 10px 0","borderBottom":"1px solid #eee","background":"none","borderWidth":"0px","display":"flex","borderStyle":"solid","justifyContent":"spaceBetween"}'>
							<div class="lable" :style='{"padding":"0 10px","color":"#666","textAlign":"left","width":"100px","fontSize":"14px","lineHeight":"24px","height":"auto"}'>:</div>
							<div :style='{"padding":"0","fontSize":"14px","lineHeight":"24px","color":"#333","flex":"1","height":"auto"}'>{{detail.shifoulingyang}}</div>
						</div>
						<div class="item" :style='{"padding":"10px","margin":"0 0 10px 0","borderBottom":"1px solid #eee","background":"none","borderWidth":"0px","display":"flex","borderStyle":"solid","justifyContent":"spaceBetween"}'>
							<div class="lable" :style='{"padding":"0 10px","color":"#666","textAlign":"left","width":"100px","fontSize":"14px","lineHeight":"24px","height":"auto"}'>:</div>
							<div :style='{"padding":"0","fontSize":"14px","lineHeight":"24px","color":"#333","flex":"1","height":"auto"}'>{{detail.shangjiazhanghao}}</div>
						</div>
						<div class="item" :style='{"padding":"10px","margin":"0 0 10px 0","borderBottom":"1px solid #eee","background":"none","borderWidth":"0px","display":"flex","borderStyle":"solid","justifyContent":"spaceBetween"}'>
							<div class="lable" :style='{"padding":"0 10px","color":"#666","textAlign":"left","width":"100px","fontSize":"14px","lineHeight":"24px","height":"auto"}'>:</div>
							<div :style='{"padding":"0","fontSize":"14px","lineHeight":"24px","color":"#333","flex":"1","height":"auto"}'>{{detail.shangjiaxingming}}</div>
						</div>
						<div class="item" :style='{"padding":"10px","margin":"0 0 10px 0","borderBottom":"1px solid #eee","background":"none","borderWidth":"0px","display":"flex","borderStyle":"solid","justifyContent":"spaceBetween"}'>
							<div class="lable" :style='{"padding":"0 10px","color":"#666","textAlign":"left","width":"100px","fontSize":"14px","lineHeight":"24px","height":"auto"}'>:</div>
							<div :style='{"padding":"0","fontSize":"14px","lineHeight":"24px","color":"#333","flex":"1","height":"auto"}'>{{detail.sfsh}}</div>
						</div>
					</div>
					
				
					
					<!--  -->
<div class="btn" :style='{"width":"100%","padding":"10px 0","flexWrap":"wrap","display":"flex","justifyContent":"flex-start"}'>
    <el-button :style='{"border":"none","cursor":"pointer","padding":"0 20px","margin":"0 10px 10px 0","outline":"none","color":"#fff","borderRadius":"4px","background":"#1e3c4f","width":"auto","lineHeight":"36px","fontSize":"14px","height":"36px","transition":"all 0.3s"}' v-if="btnAuth('chongwuxinxi','')" @click="editClick"></el-button>
    <el-button :style='{"border":"none","cursor":"pointer","padding":"0 20px","margin":"0 10px 10px 0","outline":"none","color":"#fff","borderRadius":"4px","background":"#f56c6c","width":"auto","lineHeight":"36px","fontSize":"14px","height":"36px","transition":"all 0.3s"}' v-if="btnAuth('chongwuxinxi','')" @click="delClick"></el-button>
    <el-button :style='{"border":"none","cursor":"pointer","padding":"0 20px","margin":"0 10px 10px 0","outline":"none","color":"#fff","borderRadius":"4px","background":"#67c23a","width":"auto","lineHeight":"36px","fontSize":"14px","height":"36px","transition":"all 0.3s"}' v-if="btnAuth('chongwuxinxi','')&&detail.id!=mid" @click="chatClick">TA</el-button>
</div>
				</div>
			</div>
			
			<!--  -->
			<div class="thumbnail-container" v-if="detailBanner.length > 1" :style='{"width":"58%","margin":"10px 0 0","padding":"0","display":"flex","flexWrap":"wrap","justifyContent":"flex-start"}'>
				<div :style='{"border":"1px solid #eee","width":"120px","margin":"0 10px 10px 0","position":"relative","background":"#fff","height":"90px","cursor":"pointer","transition":"all 0.3s"}' v-for="(item, index) in detailBanner" :key="index" @click="swiperClick3(item.substr(0,4)=='http'?item:baseUrl + item)">
					<img :style='{"objectFit":"cover","top":"0px","left":"0px","display":"block","width":"100%","position":"absolute","height":"100%","zIndex":"1"}' 
						v-if="item.substr(0,4)=='http'" 
						:src="item" 
						class="image">
					<img :style='{"objectFit":"cover","top":"0px","left":"0px","display":"block","width":"100%","position":"absolute","height":"100%","zIndex":"1"}' 
						v-else 
						:src="baseUrl + item" 
						class="image">
				</div>
			</div>
	
			<!--  -->
			<el-tabs class="detail" :style='{"border":"1px solid #e9e9e9","boxShadow":"0 2px 12px 0 rgba(0,0,0,0.1)","margin":"20px auto 40px","background":"#fff","display":"block","width":"100%","order":"3","borderRadius":"4px"}' v-model="activeName" type="border-card">
				<el-tab-pane label="" name="first">
					<div class="content-section" :style='{"padding":"20px","minHeight":"200px"}'>
						<div :style='{"color":"#666","lineHeight":"1.8","fontSize":"14px"}' v-html="detail.lingyangshenqing"></div>
						
						
					</div>
				</el-tab-pane>
				<el-tab-pane label="" name="second">
					<div class="content-section" :style='{"padding":"20px","minHeight":"200px"}'>
						<div :style='{"color":"#666","lineHeight":"1.8","fontSize":"14px"}' v-html="detail.shhf"></div>
						
						
					</div>
				</el-tab-pane>
			</el-tabs>
		</div>
	</div>
	</template>
<script>
  import CountDown from '@/components/CountDown';
  import axios from 'axios'
  import Swiper from "swiper";
  
  export default {
    //
    data() {
      return {
        tablename: 'chongwulingyang',
        baseUrl: '',
        breadcrumbItem: [
          {
            name: ''
          }
        ],
        title: '',
        detailBanner: [],
		userid: localStorage.getItem('frontUserid'),
		id: 0,
        detail: {},
        activeName: 'second',
        form: {
          content: '',
          userid: localStorage.getItem('frontUserid'),
          nickname: localStorage.getItem('username'),
          avatarurl: '',
        },
		showIndex: -1,
        infoList: [],
        rules: {
          content: [
            { required: true, message: '', trigger: 'blur' }
          ],
        },
        total: 1,
        pageSize: 5,
        totalPage: 1,
        storeupParams: {
          name: '',
          picture: '',
          refid: 0,
          tablename: 'chongwulingyang',
          userid: localStorage.getItem('frontUserid')
        },
        isStoreup: false,
        storeupInfo: {},
        isCrazilynum: false,
        isThumbsupnum: false,
        thumbsupOrCrazilyInfo: {},
        buynumber: 1,
        sfshVisiable: false,
        shForm: {
          sfsh: '',
          shhf: ''
        },
		centerType: false,
		shareUrl: location.href,
		swiperBigUrl: null,
      }
    },
    created() {
		if(this.$route.query.centerType) {
			this.centerType = true
		}
		
        this.init();
    },
	mounted() {
	},
    //
    methods: {
		swiperClick3(src) {
			this.swiperBigUrl = src
		},
        init() {
		  this.id = this.$route.query.id
          this.baseUrl = this.$config.baseUrl;
          this.$http.get(this.tablename + '/detail/'  + this.id, {}).then(res => {
            if (res.data.code == 0) {
              this.detail = res.data.data;
				this.title = this.detail.chongwumingcheng;
				this.detailBanner = this.detail.fengmian ? this.detail.fengmian.split(",") : [];
				this.$forceUpdate();

				this.getDiscussList(1);
				if(localStorage.getItem('frontToken')){
					this.getStoreupStatus();
					this.getThumbsupOrCrazilyStatus();
				}

            }
			if (this.detailBanner.length) {
				if (this.detailBanner[0].substr(0,4)=='http') {
					this.swiperBigUrl = this.detailBanner[0]
				} else {
					this.swiperBigUrl = this.baseUrl + this.detailBanner[0]
				}
			}
          });
        },
      onSH() {
        this.sfshVisiable = true;
      },
      shHandler(){
        this.$confirm(`?`, "", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        }).then(() => {
          this.detail.sfsh = this.shForm.sfsh;
          this.detail.shhf = this.shForm.shhf;
          this.$http.post(`${this.tablename}/update`, this.detail).then(res => {
            if (res.data.code == 0) {
              this.sfshVisiable = false;
              this.$message({
                message: "",
                type: "success",
                duration: 1500,
              });
            }
          });
        });
      },
      storeup(type) {
        if (type == 1 && !this.isStoreup) {
          this.storeupParams.name = this.title;
          this.storeupParams.picture = this.detailBanner[0];
          this.storeupParams.refid = this.detail.id;
          this.storeupParams.type = type;
          this.$http.post('storeup/add', this.storeupParams).then(res => {
            if (res.data.code == 0) {
              this.isStoreup = true;
			  this.detail.storeupnum++
			  this.$http.post('chongwulingyang/update', this.detail).then(res => {});
              this.$message({
                type: 'success',
                message: '!',
                duration: 1500,
              });
            }
          });
        }
        if (type == -1 && this.isStoreup) {
          this.$http.get('storeup/list', {params: {page: 1, limit: 1, type: 1, refid: this.detail.id, tablename: 'chongwulingyang', userid: localStorage.getItem('frontUserid')}}).then(res => {
            if (res.data.code == 0 && res.data.data.list.length > 0) {
              this.isStoreup = true;
              this.storeupInfo = res.data.data.list[0];
              let delIds = new Array();
              delIds.push(this.storeupInfo.id);
              this.$http.post('storeup/delete', delIds).then(res => {
                if (res.data.code == 0) {
                  this.isStoreup = false;
				  this.detail.storeupnum--
				  this.$http.post('chongwulingyang/update', this.detail).then(res => {});
                  this.$message({
                    type: 'success',
                    message: '!',
                    duration: 1500,
                  });
                }
              });
            }
          });
        }
      },
      getStoreupStatus(){
        if(localStorage.getItem("frontToken")) {
            this.$http.get('storeup/list', {params: {page: 1, limit: 1, type: 1, refid: this.detail.id, tablename: 'chongwulingyang', userid: localStorage.getItem('frontUserid')}}).then(res => {
              if (res.data.code == 0 && res.data.data.list.length > 0) {
                this.isStoreup = true;
                this.storeupInfo = res.data.data.list[0];
              }
            });
        }
      },
      thumbsupOrCrazily(type) {
        this.storeupParams.name = this.title;
        this.storeupParams.picture = this.detailBanner[0];
        this.storeupParams.refid = this.detail.id;
        this.storeupParams.type = type;
        this.$http.post('storeup/add', this.storeupParams).then(res => {
          if (res.data.code == 0) {
            this.getThumbsupOrCrazilyStatus();
            this.$message({
              type: 'success',
              message: '!',
              duration: 1500,
            });
          }
        });

        if (type == 21) this.detail.thumbsupnum = Number(this.detail.thumbsupnum) + 1;
        if (type == 22) this.detail.crazilynum = Number(this.detail.crazilynum) + 1;
        this.$http.post('chongwulingyang/update', this.detail).then(res => {});
      },
      cancelThumbsupOrCrazily(type) {
        let delIds = new Array();
        delIds.push(this.thumbsupOrCrazilyInfo.id);
        this.$http.post('storeup/delete', delIds).then(res => {
          if (res.data.code == 0) {
            this.isThumbsupnum = false;
            this.isCrazilynum = false;
            this.$message({
              type: 'success',
              message: '!',
              duration: 1500,
            });
          }
        });

        if (type == 21) this.detail.thumbsupnum -= 1;
        if (type == 22) this.detail.crazilynum -= 1;
        this.$http.post('chongwulingyang/update', this.detail).then(res => {});
      },
    getThumbsupOrCrazilyStatus() {
        if(localStorage.getItem("frontToken")) {
            this.$http.get('storeup/list', {params: {page: 1, limit: 1, type: 21, refid: this.detail.id, tablename: 'chongwulingyang', userid: localStorage.getItem('frontUserid')}}).then(res => {
              if (res.data.code == 0 && res.data.data.list.length > 0) {
                this.isThumbsupnum = true;
                this.thumbsupOrCrazilyInfo = res.data.data.list[0];
              }
            });

            this.$http.get('storeup/list', {params: {page: 1, limit: 1, type: 22, refid: this.detail.id, tablename: 'chongwulingyang', userid: localStorage.getItem('frontUserid')}}).then(res => {
              if (res.data.code == 0 && res.data.data.list.length > 0) {
                this.isCrazilynum = true;
                this.thumbsupOrCrazilyInfo = res.data.data.list[0];
              }
            });
        }
    },
      curChange(page) {
        this.getDiscussList(page);
      },
      prevClick(page) {
        this.getDiscussList(page);
      },
      nextClick(page) {
        this.getDiscussList(page);
      },
		// 
		backClick(){
			this.$router.go(-1); // 

		},
		// 
		download(file){
			if(!file) {
				this.$message({
				  type: 'error',
				  message: '',
				  duration: 1500,
				});
				return;
			}
		  let arr = file.replace(new RegExp('upload/', "g"), "")
		  axios.get(this.baseUrl + '/file/download?fileName=' + arr, {
		  	headers: {
		  		token: localStorage.getItem("frontToken")
		  	},
		  	responseType: "blob"
		  }).then(({
		  	data
		  }) => {
		  	const binaryData = [];
		  	binaryData.push(data);
		  	const objectUrl = window.URL.createObjectURL(new Blob(binaryData, {
		  		type: 'application/pdf;chartset=UTF-8'
		  	}))
		  	const a = document.createElement('a')
		  	a.href = objectUrl
		  	a.download = arr
		  	// a.click()
		  	// 
		  	a.dispatchEvent(new MouseEvent('click', {
		  		bubbles: true,
		  		cancelable: true,
		  		view: window
		  	}))
		  	window.URL.revokeObjectURL(data)
		  },err=>{
			  axios.get((location.href.split(this.$config.name).length>1 ? location.href.split(this.$config.name)[0] :'') + this.$config.name + '/file/download?fileName=' + arr, {
			  	headers: {
			  		token: localStorage.getItem("frontToken")
			  	},
			  	responseType: "blob"
			  }).then(({
			  	data
			  }) => {
			  	const binaryData = [];
			  	binaryData.push(data);
			  	const objectUrl = window.URL.createObjectURL(new Blob(binaryData, {
			  		type: 'application/pdf;chartset=UTF-8'
			  	}))
			  	const a = document.createElement('a')
			  	a.href = objectUrl
			  	a.download = arr
			  	// a.click()
			  	// 
			  	a.dispatchEvent(new MouseEvent('click', {
			  		bubbles: true,
			  		cancelable: true,
			  		view: window
			  	}))
			  	window.URL.revokeObjectURL(data)
			  })
		  })
      },
      getDiscussList(page) {
        this.$http.get('discusschongwulingyang/list', {params: {page, limit: this.pageSize, refid: this.detail.id}}).then(res => {
          if (res.data.code == 0) {
            this.infoList = res.data.data.list;
            this.total = res.data.data.total;
            this.pageSize = res.data.data.pageSize;
            this.totalPage = res.data.data.totalPage;
          }
        });
      },
	  discussEnter(index){
		  this.showIndex = index
	  },
	  discussLeave(){
		  this.showIndex = -1
	  },
	  discussDel(id){
		  this.$confirm('')
		    .then(_ => {
		      this.$http.post('discusschongwulingyang/delete',[id]).then(res=>{
				  if(res.data&&res.data.code==0){
					  this.addDiscussNum(1)
					  this.$message({
					    type: 'success',
					    message: '!',
					    duration: 1500,
						onClose: () => {
							this.getDiscussList(1);
						}
					  });
				  }
			  })
		    }).catch(_ => {});
	  },
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.form.refid = this.detail.id;
            this.form.avatarurl = localStorage.getItem('frontHeadportrait')?localStorage.getItem('frontHeadportrait'):'';
            this.$http.post('discusschongwulingyang/add', this.form).then(res => {
              if (res.data.code == 0) {
                this.form.content = '';
				this.addDiscussNum(2)
                this.getDiscussList(1);
                this.$message({
                  type: 'success',
                  message: '!',
                  duration: 1500,
                });
              }
            });
          } else {
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
	  addDiscussNum(type){
		  if(type==2){
			  this.detail.discussnum++
		  }else if(type==1){
			  if(this.detail.discussnum!=0){
				  this.detail.discussnum--
			  }else{
				  this.detail.discussnum = 0
			  }
		  }
		  this.$http.post('chongwulingyang/update',this.detail).then(res=>{
			  
		  })
	  },


		// 
		btnAuth(tableName,key){
			if(this.centerType){
				return this.isBackAuth(tableName,key)
			}else{
				return this.isAuth(tableName,key)
			}
		},
		// 
		editClick(){
			this.$router.push(`/index/chongwulingyangAdd?type=edit&&id=${this.detail.id}`);
		},
		// 
		async delClick(){
			await this.$confirm('')
			  .then(_ => {
			    this.$http.post('chongwulingyang/delete', [this.detail.id]).then(async res => {
					if (res.data.code == 0) {
						this.$http.get('storeup/list',{params:{
							page: 1,
							limit: 100,
							refid: this.detail.id,
							tablename: 'chongwulingyang',
						}}).then(async obj=>{
							if(obj.data&&obj.data.code==0){
								let arr = []
								for(let x in obj.data.data.list){
									arr.push(obj.data.data.list[x].id)
								}
								if(arr.length){
									await this.$http.post('storeup/delete',arr).then(()=>{})
								}
								this.$message({
									type: 'success',
									message: '!',
									duration: 1500,
									onClose: () => {
										history.back()
									}
								});
							}
						})
					}
			    });
			  }).catch(_ => {});
		},
    },
    components: {
      CountDown
    }
  }
</script>

