						<div @click="storeup(-1)" v-show="isStoreup" :style='{"border":"none","cursor":"pointer","padding":"0 16px","lineHeight":"36px","borderRadius":"4px","background":"#1e3c4f","transition":"all 0.3s","width":"auto"}'><i v-if="false" :style='{"color":"#fff","fontSize":"16px"}' class="el-icon-star-on"></i><span :style='{"color":"#eee","fontSize":"14px"}'>({{detail.storeupnum}})</span></div>
