					<!-- / -->
					<div class="zancai" :style='{"border":"1px solid #eee","padding":"10px","margin":"20px 0","background":"#f9f9f9","display":"flex","width":"100%","borderRadius":"4px","justifyContent":"center"}'>
						<div :style='{"cursor":"pointer","padding":"8px 16px","margin":"0 10px","borderRadius":"4px","alignItems":"center","background":"#f0f7ff","display":"flex","width":"auto","justifyContent":"center","transition":"all 0.3s"}' v-if="!isThumbsupnum && !isCrazilynum" class="zan" @click="thumbsupOrCrazily(21)">
							<i :style='{"color":"#1e3c4f","margin":"0 5px 0 0","fontSize":"16px"}' class="iconfont icon-dianzan"></i>
							<span :style='{"color":"#1e3c4f","margin":"0 3px","fontSize":"14px"}'> ({{detail.thumbsupnum}})</span>
						</div>
						<div :style='{"cursor":"pointer","padding":"8px 16px","margin":"0 10px","borderRadius":"4px","alignItems":"center","background":"#fff0f0","display":"flex","width":"auto","justifyContent":"center","transition":"all 0.3s"}' v-if="!isThumbsupnum && !isCrazilynum" class="cai" @click="thumbsupOrCrazily(22)">
							<i :style='{"color":"#f56c6c","margin":"0 5px 0 0","fontSize":"16px"}' class="iconfont icon-iconfontzan"></i>
							<span :style='{"color":"#f56c6c","margin":"0 3px","fontSize":"14px"}'> ({{detail.crazilynum}})</span>
						</div>
						<div :style='{"cursor":"pointer","padding":"8px 16px","margin":"0 10px","borderRadius":"4px","alignItems":"center","background":"#e6f7ff","display":"flex","width":"auto","justifyContent":"center","transition":"all 0.3s"}' v-if="isThumbsupnum" class="zan" @click="cancelThumbsupOrCrazily(21)">
							<i :style='{"color":"#1e3c4f","margin":"0 5px 0 0","fontSize":"16px"}' class="iconfont icon-dianzan"></i>
							<span :style='{"color":"#1e3c4f","margin":"0 3px","fontSize":"14px"}'> ({{detail.thumbsupnum}})</span>
						</div>
						<div :style='{"cursor":"pointer","padding":"8px 16px","margin":"0 10px","borderRadius":"4px","alignItems":"center","background":"#ffeded","display":"flex","width":"auto","justifyContent":"center","transition":"all 0.3s"}' v-if="isCrazilynum" class="cai" @click="cancelThumbsupOrCrazily(22)">
							<i :style='{"color":"#f56c6c","margin":"0 5px 0 0","fontSize":"16px"}' class="iconfont icon-iconfontzan"></i>
							<span :style='{"color":"#f56c6c","margin":"0 3px","fontSize":"14px"}'> ({{detail.crazilynum}})</span>
